<template>
  <div class="notic">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>实用信息</BreadcrumbItem>
        <BreadcrumbItem>最新公告</BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>
          <router-link :to="{ name:'noticedetail', params: { noticId: item.aId }}" v-for="(item, index) in listData" :key="index">
            <li class="list-item">
              <p class="title">{{item.sTitle}}</p>
              <p class="time">{{item.time | dateFormat}}</p>
            </li>
          </router-link>

        </ul>
      </div>
      <Page class="page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/banner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 16,
      listData: []
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "Notice",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.list {
  margin-bottom: 50px;
  a {
    &:nth-child(odd) {
      .list-item {
        background-color: #f2f2f2;
        margin-top: 2px;
      }
    }
    .list-item {
      &:hover {
        box-shadow: 0 0 1px 1px rgba(0, 0, 0, .1);
      }
    }
  }
  .list-item {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    .title {
      color: #3da0f5;
      height: 45px;
      line-height: 45px;
      font-size: 16px;
    }
    .time {
      height: 45px;
      line-height: 45px;
      font-size: 15px;
      color: #000;
    }
  }
}
.page {
  margin-bottom: 20px;
}
</style>





