<template>
  <div class="ticket">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>门票特产</BreadcrumbItem>
        <BreadcrumbItem>景区门票</BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>
          <li class="list-item" v-for="(item, index) in listData" :key="index">
            <Card>
              <div class="card">
                <img :src="item.pic">
                <div class="card-content">
                  <div class="info">
                    <h3>{{item.title}}</h3>
                    <p>
                      <span class="sign">￥</span>
                      <span class="price">{{item.price*item.discount}}</span> 起
                    </p>
                    <template v-if="item.discount !== 1">
                      <p class="disc">{{item.discount*10}}折/省
                        <span class="saved-price">￥{{item.price*(1*10-(item.discount*10))/10}}</span>
                      </p>
                    </template>
                  </div>
                  <div class="btn">
                    <a :href="item.url">抢购</a>
                  </div>
                </div>
              </div>
            </Card>
          </li>
        </ul>
        <Page class="common-page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: "",
      pageTotal: 0,
      pageSize: 16,
      listData: []
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/commercial/", {
          params: {
            name: "Ticket",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
            _this.topImage = res.data.topPic;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.list {
  margin-bottom: 50px;
  text-align: left;
}
.list-item {
  display: inline-block;
  width: 24%;
  margin: 0 0.5% 30px 0.5%;
  text-align: center;
  .card {
    height: 260px;
    img {
      width: 100%;
      height: 60%;
      display: block;
    }
  }
  .card-content {
    display: flex;
    justify-content: space-between;
    height: 40%;
    margin-top: 15px;
    position: relative;
    .info {
      text-align: left;
      font-weight: bold;
      .sign {
        color: #ff6a3a;
      }
      .price {
        color: #ff6a3a;
        font-size: 20px;
        font-weight: bold;
      }
      p {
        &:first-of-type {
          margin: 2px 0;
        }
      }
      .disc {
        color: #dadada;
        font-size: 16px;
      }
      .saved-price {
        color: #ff6a3a;
      }
    }
    .btn {
      position: absolute;
      right: 0;
      bottom: 30px;
      a {
        padding: 8px 15px;
        background: #dc2632;
        border-radius: 10px;
        color: #fff;
        font-size: 17px;
        font-weight: bold;
      }
    }
  }
}
</style>





