<template>
  <div class="price">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>实用信息</BreadcrumbItem>
        <BreadcrumbItem>交通路线</BreadcrumbItem>
      </Breadcrumb>
      <Row>
        <i-col span=12>
          <div class="map">
            <Card class="outer-card">
            </Card>
            <Card class="inner-card">
              <div id="map" style="width:526px; height:468px"></div>
            </Card>
          </div>
        </i-col>
        <i-col span=12>
          <div class="common-title">
            <h1>交通路线</h1>
            <h3>Traffic routes</h3>
          </div>
          <div class="traffic-item">
            <h3>外部交通</h3>
            <p>茶卡盐湖，别称茶卡或达布逊淖尔，是位于青海省海西蒙古族藏族自治州乌兰县茶卡镇附近的一个盐湖。“茶卡”是藏语，意即盐池，也就是青</p>
          </div>
          <div class="traffic-item">
            <h3>内部交通</h3>
            <p>游客进入景区乘坐景区观光车，乘车费每人20元（不设站点，持票人在景区内可多次乘坐） </p>
          </div>
          <div class="traffic-item">
            <h3>地图定位</h3>
            <div class="map-link">
              <a target="_blank" href="https://www.amap.com/search?query=chakayanhu&city=210200&geoobj=121.520207%7C38.853149%7C121.530507%7C38.857118&zoom=17">高德地图</a>
              <a  target="_blank" href="https://map.baidu.com/">百度地图</a>
            </div>

          </div>
        </i-col>
      </Row>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/trafficBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      listData: []
    };
  },
  methods: {
    fetchMapApi() {
      var _this = this;
      var script = document.createElement("script");
      script.type = "text/javascript";
      script.src ="http://webapi.amap.com/maps?v=1.4.4&key=04332b398b1567babd61b955d84377fb";
      _this.$el.appendChild(script);
      script.onload = function() {
        _this.$nextTick(function() {
          var map = new AMap.Map("map", {
            zoom: 10,
            center: [99.111073,36.698561]
          });
        });
      };
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.fetchMapApi();
  }
};
</script>

<style lang="scss" scoped>
.ivu-row {
  margin-bottom: 30px;
  .map {
    position: relative;
    width: 90%;
  }
}
.outer-card {
  width: 100%;
  height: 500px;
}
.inner-card {
  position: absolute;
  width: 100%;
  height: 500px;
  top: 10px;
  right: -10px;
}
.traffic-item {
  margin: 20px 0;
  text-align: left;
  h3 {
    color: #3da0f5;
  }
}
</style>






