<template>
  <div class="container">
    <div class="image" :style="{backgroundImage: 'url('+ getImage + ')'}"></div>
    <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
import defaultImage from "../assets/banner.png";

export default {
  name: "TopImage",
  props: {
    image: {
      type: String,
      default: defaultImage
    }
  },
  data() {
    return {
      spinShow: true
    };
  },
  computed: {
    getImage() {
      if (this.image === "" || this.image === null) {
        var _this = this;
        var image = new Image();
        image.src = defaultImage;
        image.onload = function() {
          _this.spinShow = false;
        };
        return defaultImage;
      } else {
        var _this = this;
        var image = new Image();
        image.src = _this.image;
        image.onload = function() {
          _this.spinShow = false;
        };
        return this.image;
      }
    }
  }
};
</script>

<style scoped>
.container {
  position: relative;
}
.image {
  width: 100%;
  height: 500px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}
</style>




