<template>
  <div>
    <div class="categy-name">{{categy}}</div>
    <div class="images-container">
      <div v-for="(item, index) in datalist" :key="index" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}" @click="handleImageClick(index)" class="image-item hvr-grow"></div>
    </div>
    <div class="spin" v-if="hasData">
      <Button type="info" v-if="showReadMore" @click="handleReadMore">Read More</Button>
      <Spin v-else fix style="backgroundColor:transparent">
        <Icon type="load-a" size=18 class="spin-icon-load"></Icon>
      </Spin>
    </div>
    <div class="no-result" v-else>
      <h3>No More</h3>
    </div>
    <Modal v-model="modal" width="80%" class-name="vertical-center-modal">
      <div style="text-align:center">
        <swiper :options="swiperOption" ref="imageSwiper">
          <swiper-slide class="slide-1" v-for="(item, index) in datalist" :key="index">
            <div :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}" class="swiper-item"></div>
          </swiper-slide>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
      </div>
      <div slot="footer" class="modal-footer">
        <div class="swiper-pagination images-pagination" slot="pagination"></div>
      </div>
    </Modal>
  </div>
</template>

<script>
import config from "../config";
export default {
  name: "CategyItem",
  props: {
    categy: {
      type: String,
      default: "默认分类"
    },
    categyindex:{
      type: Number,
      required: true
    },
    list: {
      type: Array,
      default: []
    },
    url: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      showReadMore: true,
      hasData:true,
      datalist: this.list,
      pageSize: 10,
      page: 2,
      modal: false,
      swiperOption: {
        spaceBetween: 30,
        effect: "fade",
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    };
  },
  methods: {
    switchItem(item) {
      return item.split("|");
    },
    handleImageClick(index) {
      this.modal = true;
      this.$refs.imageSwiper.swiper.slideTo(index, 1000, false);
    },
    handleReadMore() {
      var _this = this;
      _this.showReadMore = false;
      _this.$http
        .get(config.ApiRoot + "/scenery/", {
          params: {
            page: _this.page,
            pageCount: _this.pageSize,
            category: _this.categyindex
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.datalist = _this.datalist.concat(res.data.list[0].list);
            _this.showReadMore = true;
            _this.page++;
          }else{
            _this.hasData = false;
          }
        })
        .catch(function(error) {
          _this.showReadMore = true;
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  }
};
</script>

<style  lang="scss" scoped>
.images-container {
  display: flex;
  flex-wrap: wrap;
  .image-item {
    height: 160px;
    width: calc(20% - 8px);
    margin: 0 4px 8px 4px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
}
.categy-name {
  font-weight: bold;
  font-size: 18px;
  text-align: left;
  margin-left: 4px;
  padding: 10px 0;
  color: #fff;
}
.spin,.no-result {
  position: relative;
  height: 80px;
  line-height: 80px;
}
.spin-icon-load {
  animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
  from {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;

  .ivu-modal {
    top: 0;
  }
  .images-pagination {
    left: 0;
    right: 0;
    bottom: 2px;
  }
}
.swiper-item {
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #fff;
  background-size: 100% 100%;
}
</style>




