<template>
  <div class="price">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>实用信息</BreadcrumbItem>
        <BreadcrumbItem>票价时间</BreadcrumbItem>
      </Breadcrumb>
      <div class="detail">
        <div class="common-title">
          <h1>开放时间及票价</h1>
          <h3>Opening hours and fares</h3>
        </div>
        <div class="item">
          <h1>开放时间
            <span>/OPENING</span>
            <div class="border-img"></div>
          </h1>
          <div class="time" v-for="(item,index) in openTime" :key="index">
            <p>{{item[0]+'：'+item[1]}}</p>
            <p>营业时间：{{item[2]}}</p>
          </div>
        </div>
        <div class="item half">
          <h1>门票信息
            <span>/TICKET</span>
            <div class="border-img"></div>
          </h1>
          <div class="ticket">
            <p v-for="(item,index) in ticketInfo" :key="index">{{item[0]}}：{{item[1]}}
              <a v-if="item[2]!=''" :href="item[2]">点击购买</a>
            </p>
          </div>
        </div>
        <div class="item half">
          <h1>优惠政策
            <span>/DISCOUNT</span>
            <div class="border-img"></div>
          </h1>
           <i-input v-model="discount" disabled :autosize="true" type="textarea" class="common-textarea" ></i-input>
        </div>
        <div class="img">
          <img src="../assets/ticket.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/banner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      openTime: [],
      ticketInfo: [],
      discount: ""
    };
  },
  methods: {
    fetchData() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/_Description", {})
        .then(function(res) {
          if (res.data) {
            _this.openTime = res.data.times;
            _this.ticketInfo = res.data.info;
            _this.discount = res.data.text;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.fetchData();
  }
};
</script>

<style lang="scss" scoped>
.detail {
  position: relative;
  min-height: 550px;
  .half {
    width: calc(100% - 500px);
    margin: 50px 0;
  }
  .item {
    text-align: left;
    h1 {
      color: #de3a6e;
      span {
        color: #d5d5d5;
      }
    }
    .border-img {
      height: 2px;
      background: url("../assets/borderImage.png");
    }
    .time {
      display: flex;
      width: calc(100% - 500px);
      padding: 20px 0 0 0;
      p {
        font-size: 16px;
        margin-right: 30px;
        width: 50%;
      }
    }
    .ticket {
      padding: 20px 0 0 0;
      p {
        font-size: 16px;
        margin: 20px 0 0 0;
      }
      a {
        margin-left: 30px;
      }
    }
    .discount {
      font-size: 16px;
      padding: 20px 0 0 0;
    }
  }
  .img {
    position: absolute;
    right: 0;
    top: 200px;
    width: 500px;
    background-color: rgb(245, 247, 249);
    img {
      width: 100%;
    }
  }
}
</style>






