<template>
  <div class="item">
    <h3>{{title}}</h3>
    <p>{{abstract}}</p>
    <router-link :to="{ name:'noticedetail', params: { noticId: aid }}">
      <span v-if="showReadmore">READ MORE</span>
    </router-link>
     <Button v-if="showReadmore" class="btn" type="primary" icon="close-round" @click="closeClick"></Button>
    <Button v-if="!showReadmore" class="btn" type="primary" icon="plus-round" @click="plusClick"></Button>
  </div>
</template>

<script>
export default {
  name: "ArticleCarouselItem",
  data: function() {
    return {
      showReadmore: false
    };
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    abstract: {
      type: String,
      default: ""
    },
    aid: {
      type: String,
      default: ""
    }
  },
  methods: {
    plusClick() {
      this.showReadmore = true;
    },
    closeClick() {
      this.showReadmore = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../styles/variables.scss";
.item {
  position: relative;
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 0;
}
h3 {
  margin-bottom: 10px;
}
p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  margin: 10px 50px 0 0;
}
.btn {
  position: absolute;
  right: 0;
  top: 20px;
  background-color: #0085f6;
  color: #fff;
  padding: 5px 10px;
}
</style>




