<template>
  <div class="complaint">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>景区相关</BreadcrumbItem>
        <BreadcrumbItem>投诉建议</BreadcrumbItem>
      </Breadcrumb>
      <div class="container">
        <div class="top">
          <div class="label">请你留言</div>
          <div class="tele">
            <Icon type="ios-telephone"></Icon> 投诉电话：0937-8888888</div>
        </div>
        <i-input class="complaint" v-model="text" type="textarea" :autosize="{minRows: 15,maxRows: 20}" placeholder="请在此处输入留言内容，我们会尽快与您联系。"></i-input>
        <div class="btm">
          <div class="labels">
            <div class="label-input">
              <span class="label">姓名</span>
              <i-input v-model="name" clearable style="width: 200px" placeholder="输入姓名"></i-input>
            </div>
            <div class="label-input">
              <span class="label">电话</span>
              <i-input v-model="phone" clearable style="width: 200px" placeholder="输入电话"></i-input>
            </div>
          </div>
          <Button type="primary" @click="handleSubmit">提交</Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/scanBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      text: "",
      name: "",
      phone: ""
    };
  },
  methods: {
    handleSubmit() {
      if (this.checkValue()) {
        this.submitData();
      }
    },
    submitData() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/feedback/", {
          params: {
            name: this.name,
            phone: this.phone,
            text: this.text,
            plat: 1
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.$Notice.success({
              title: "提交成功",
              desc: "感谢您的反馈！"
            });
          } else {
            var errormsg = "";
            switch (res.data.code) {
              case -13:
                errormsg = "您今日的反馈数量已达上限，请明日再来。";
                break;
              case -14:
                errormsg = "姓名过长，请输入正确的姓名。";
                break;
              case -15:
                errormsg = "手机号码格式不正确。";
                break;
              default:
                break;
            }
            _this.$Notice.error({
              title: "提交失败",
              desc: errormsg
            });
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    checkValue() {
      var result = true;
      if (this.text == "") {
        this.$Notice.info({
          title: "提示",
          desc: "反馈内容不能为空。"
        });
        result = false;
      }
      if (this.phone == "") {
        this.$Notice.info({
          title: "提示",
          desc: "电话号码不能为空。"
        });
        result = false;
      }
      return result;
    }
  },
  computed: {},
  watch: {},
  components: {
    TopImage
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
.top {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  align-items: flex-end;
  margin-bottom: 10px;
}
.complaint {
  margin-bottom: 20px;
}
.btm {
  display: flex;
  justify-content: space-between;
  .labels {
    display: flex;
    .label-input {
      display: flex;
      margin-right: 10px;
    }
  }
}
.label {
  padding: 5px 8px;
  background-color: #1594e4;
  font-size: 14px;
  color: #fff;
}
</style>



