<template>
  <div class="home">
    <div class="carousel-section">
      <swiper :options="carouselOption">
        <swiper-slide v-for="(item,index) in carousel" :key="index">
          <a :href="item._url">
            <div class="carousel-item" :style="{backgroundImage: 'url('+ item.url + ')'}"></div>
          </a>

        </swiper-slide>
        <div class="swiper-pagination" id="carousel_pagination" slot="pagination"></div>
        <div class="swiper-button-prev" id="carousel_prev" slot="button-prev"></div>
        <div class="swiper-button-next" id="carousel_next" slot="button-next"></div>
      </swiper>
    </div>
    <div class="imagewall-section">
      <Row>
        <i-col v-for="(item,index) in wallList" span="6" :key="index" :style="{position: 'relative'}">
          <div class="imagewall-item" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}"></div>
          <div class="item-title">
            <router-link to="/scenery">
              <Tag type="dot">{{switchItem(item)[2]}}</Tag>
            </router-link>
          </div>
        </i-col>
      </Row>
    </div>
    <TopImage :image="topImage" />
    <div class="ticket-section">
      <Row type="flex" justify="center" align="middle">
        <i-col span="12">
          <div class="section-title">
            时间
            <span>·</span> 票务
          </div>
          <div class="time" v-for="(item,index) in openTime" :key="index">
            <p>{{item[0]+'：'+item[1]}}</p>
            <p>营业时间：{{item[2]}}</p>
          </div>
          <div class="more">
            <router-link to="/price">更多</router-link>
          </div>
        </i-col>
        <i-col span="12">
          <div class="video">
            <video :src="videoUrl" preload="preload" controls="controls" width="100%" height="100%"></video>
          </div>
        </i-col>
      </Row>
    </div>
    <div class="news-section">
      <Row type="flex" justify="center" align="middle">
        <i-col span="24">
          <div class="section-title">
            新闻资讯
          </div>
          <div class="news">
            <div v-for="(item,index) in noticeList" :key="index" class="news-item">
              <h3>{{item.sTitle}}</h3>
              <p>{{item.sAbstract}}</p>
              <router-link :to="{ name:'noticedetail', params: { noticId: item.aId }}">更多</router-link>
            </div>
          </div>
        </i-col>
      </Row>
    </div>
    <div class="imagescroll-sction">
      <swiper :options="swiperOption" class="swiper">
        <swiper-slide v-for="(item,index) in scrollList" :key="index" class="scroll-item">
          <router-link to="/scenery">
            <div class="image" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}"></div>
            <p class="title">{{switchItem(item)[2]}}</p>
          </router-link>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/banner.png";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      dataSrc: config.ApiRoot + "/js/_Carousel.js",
      videoUrl: "",
      wallList: [],
      scrollList: [],
      topImage: TopBanner,
      carousel: [],
      openTime: [],
      ticketInfo: [],
      discount: "",
      noticeList: [],
      swiperOption: {
        pagination: {
          el: ".swiper-pagination",
          type: "progressbar"
        },
        slidesPerView: 3,
        spaceBetween: 30,
        autoplay: false,
        freeMode: true,
        mousewheel: true
      },
      carouselOption: {
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: {
          delay: 6000,
          disableOnInteraction: false
        },
        pagination: {
          el: "#carousel_pagination",
          clickable: true
        },
        navigation: {
          nextEl: "#carousel_prev",
          prevEl: "#carousel_next"
        }
      }
    };
  },
  computed: {},
  methods: {
    fetchJSData() {
      var _this = this;
      var script = document.createElement("script");
      script.type = "text/javascript";
      script.src = this.dataSrc;
      _this.$el.appendChild(script);
      script.onload = function() {
        _this.$nextTick(function() {
          _this.topImage = _Home.top.url;
          _this.carousel = _Home.carousel;
          _this.videoUrl = _Home.video;
        });
      };
    },
    fetchceneryData() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/scenery/", {
          params: {
            pageCount: 20,
            home: 1
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.wallList = res.data.list.slice(0, 4);
            _this.scrollList = res.data.list.slice(4);
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    fetchPriceData() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/_Description", {})
        .then(function(res) {
          if (res.data) {
            _this.openTime = res.data.times;
            _this.ticketInfo = res.data.info;
            _this.discount = res.data.text;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    fetchNoticeData() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "Notice",
            page: 1,
            pageCount: 6
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.noticeList = res.data.list;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    switchItem(item) {
      return item.split("|");
    }
  },
  mounted() {
    this.fetchJSData();
    this.fetchceneryData();
    this.fetchPriceData();
    this.fetchNoticeData();
  },
  components: {
    TopImage
  }
};
</script>

<style lang="scss" scoped>
.imagewall-section {
  .imagewall-item {
    height: 230px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  .item-title {
    position: absolute;
    bottom: 10px;
    left: 10px;
  }
}
.carousel-item {
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #fff;
  background-size: 100% 100%;
}
.ticket-section {
  height: 450px;
  padding: 50px 60px;
  background-image: url("../assets/ticketBackground.png");
  background-repeat: repeat;
  background-size: contain;
  color: #fff;
  .time {
    padding-bottom: 10px;
    display: flex;
    p {
      text-align: left;
      font-size: 18px;
      margin-right: 20px;
      width: 50%;
    }
  }
  .price-info {
    text-align: left;
  }
  .more {
    position: absolute;
    top: 0;
    right: 0;
    width: 15px;
    margin: 0 auto;
    line-height: 17px;
    font-size: 15px;
    a {
      color: #fff;
      &:hover {
        color: #2d8cf0 !important;
      }
    }
  }
  .ivu-row-flex {
    height: 100%;
    position: relative;
  }
  .video {
    width: 480px;
    height: 360px;
    margin: 0 auto;
  }
}
.news-section {
  height: 400px;
  padding: 50px 60px;
  background-image: url("../assets/newsBackground.png");
  background-repeat: repeat;
  background-size: contain;
  color: #fff;
  .news {
    display: flex;
    flex-wrap: wrap;
    .news-item {
      width: 24%;
      margin-right: 1%;
      text-align: left;
      padding-bottom: 30px;
      h3 {
        margin-bottom: 10px;
        font-size: 17px;
      }
      p {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        margin-bottom: 10px;
        max-height: 42px;
      }
      a {
        color: #fff;
        padding: 4px 8px;
        border: 1px solid;
        border-radius: 5px;
        display: block;
        width: 45px;
        text-align: center;
        &:hover {
          color: #2d8cf0 !important;
        }
      }
    }
  }
  .ivu-row-flex {
    height: 100%;
  }
  .ivu-col {
    height: 100%;
  }
}
.imagescroll-sction {
  height: 300px;
  background-image: url("../assets/scrollSectionBackground.png");
  background-position: center;
  .swiper {
    height: 100%;
    .swiper-pagination {
      top: auto;
      bottom: 0;
    }
  }
  .scroll-item {
    height: 80%;
    width: 33%;
    margin-right: calc(1%/3);
    flex: none;
    .image {
      height: 100%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 100%;
    }
    .title {
      height: 20%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      color: #fff;
    }
  }
}
.section-title {
  text-align: left;
  font-size: 20px;
  padding-bottom: 30px;
  span {
    font-size: 50px;
    font-weight: bold;
    position: relative;
    top: 7px;
    line-height: 20px;
  }
}
</style>



