import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Notice from './views/Notice.vue'
import NoticeDetail from './views/NoticeDetail.vue'
import Price from './views/Price.vue'
import Traffic from './views/Traffic.vue'
import Scenery from './views/Scenery.vue'
import Overview from './views/Overview.vue'
import TwoDview from './views/TwoDview.vue'
import Hotels from './views/Hotel.vue'
import Food from './views/Food.vue'
import Ticket from './views/Ticket.vue'
import Activity from './views/Activity.vue'
import ActivityDetail from './views/ActivityDetail.vue'
import Travels from './views/Travels.vue'
import TravelsDetail from './views/TravelsDetail.vue'
import Scan from './views/Scan.vue'
import Contract from './views/Contract.vue'
import Aboutus from './views/Aboutus.vue'


Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/notice',
      name: 'notice',
      component: Notice
    },
    {
      path: '/noticedetail/:noticId',
      name: 'noticedetail',
      component: NoticeDetail
    },
    {
      path: '/price',
      name: 'price',
      component: Price
    },
    {
      path: '/traffic',
      name: 'traffic',
      component: Traffic
    },
    {
      path: '/scenery',
      name: 'scenery',
      component: Scenery
    },

    {
      path: '/overview',
      name: 'overview',
      component: Overview
    },
    {
      path: '/twoDview',
      name: 'twoDview',
      component: TwoDview
    },
    {
      path: '/hotels',
      name: 'hotel',
      component: Hotels
    },
    {
      path: '/food',
      name: 'food',
      component: Food
    },
    {
      path: '/ticket',
      name: 'ticket',
      component: Ticket
    },
    {
      path: '/activity',
      name: 'activity',
      component: Activity
    },
    {
      path: '/activityDetail/:activityId',
      name: 'activityDetail',
      component: ActivityDetail
    },
    {
      path: '/travels',
      name: 'travels',
      component: Travels
    },
    {
      path: '/travelsDetail/:travelsId',
      name: 'travelsDetail',
      component: TravelsDetail
    },
    {
      path: '/scan',
      name: 'scan',
      component: Scan
    },
    {
      path: '/contract',
      name: 'contract',
      component: Contract
    },
    {
      path: '/aboutus',
      name: 'aboutus',
      component: Aboutus
    }
  ]
})



