<template>
  <div class="notic">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>门票特产</BreadcrumbItem>
        <BreadcrumbItem>美食特产</BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>

          <li class="list-item" v-for="(item, index) in listData" :key="index">
            <a :href="item.url">
              <Card>
                <div class="card">
                  <img :src="item.pic">
                </div>
              </Card>
              <div class="info">
                <Row type="flex" justify="center" align="middle" class="info-container">
                  <i-col span=12>
                    <h3>{{item.name}}</h3>
                    <p>{{item.effect}}</p>
                  </i-col>
                  <i-col span=12>
                    <h2>{{item.price}}{{item.company}}</h2>
                  </i-col>
                </Row>
              </div>
            </a>
          </li>

        </ul>
        <Page class="common-page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: "",
      pageTotal: 0,
      pageSize: 9,
      listData: []
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/commercial/", {
          params: {
            name: "Specialty",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
            _this.topImage = res.data.topPic;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.list {
  margin-bottom: 50px;
  text-align: left;
}
.list-item {
  display: inline-block;
  width: 31%;
  margin: 0 1% 30px 1%;
  text-align: center;
  position: relative;
  .card {
    height: 260px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .info {
    background-image: url("../assets/foodItem.png");
    background-position: center top;
    height: 80px;
    position: absolute;
    bottom: 0;
    left: 16px;
    width: calc(100% - 32px);
    .info-container {
      height: 100%;
      color: #fff;
    }
  }
}
</style>





