<template>
  <div class="scenery">
    <TopImage :image="topImage" />
    <div class="">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>实用信息</BreadcrumbItem>
        <BreadcrumbItem>景区美景</BreadcrumbItem>
      </Breadcrumb>
      <div class="image-wall">
        <div class="image-wall-item" v-for="(categy, index) in imageList" :key="index">
          <CategyItem :categy="categy.name" :list="categy.list" :url="getMoreUrl" :categyindex="index" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TopImage from "@/components/TopImage.vue";
import CategyItem from "@/components/CategyItem.vue"
import config from "../config";
export default {
  data() {
    return {
      topImage: "",
      pageTotal: 0,
      imageList: [],
      getMoreUrl: config.ApiRoot + "/scenery/"
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      _this.$Spin.show();
      this.$http
        .get(config.ApiRoot + "/scenery/")
        .then(function(res) {
          _this.$Spin.hide();
          if (res.data.code === 0) {
            _this.imageList = res.data.list;
            _this.pageCount = res.data.count;
            _this.topImage = res.data.topPic;
          }
        })
        .catch(function(error) {
          _this.$Spin.hide();
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    switchItem(item) {
      return item.split("|");
    }
  },
  components: {
    TopImage,
    CategyItem
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.image-wall {
  background-color: #161616;
  .common-page {
    padding: 20px 0;
  }
}
</style>





