var dateFormat = (data)=>{
  data = data || "";
  return data.slice(0,10);
}

var imageUrlFormat = (string)=>{
  if(string == null || string == "") return ""
  else item.split('|')[1];
}

var imageTitleFormat = (string)=>{
  if(string == null || string == "") return ""
  else item.split('|')[2];
}

export default {
  dateFormat,
  imageUrlFormat,
  imageTitleFormat
}


