<template>
  <div class="activity-detail">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>景区概况</BreadcrumbItem>
        <BreadcrumbItem to='/travels'>旅游手记</BreadcrumbItem>
        <BreadcrumbItem>详情</BreadcrumbItem>
      </Breadcrumb>
      <div class="details">
        <div class="top">
          <h2>{{data.sTitle}}</h2>
          <p>发布时间： {{data.time}}</p>
        </div>
        <div class="detail">
          <img :src="data.sTopPic" />
          <div v-html="data.sHtml"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/activityBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 16,
      data: {}
    };
  },
  methods: {
    fetchDetail() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/" + this.$route.params.travelsId)
        .then(function(res) {
          if (res.data) {
            _this.data = res.data;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    $route: "fetchDetail"
  },
  created() {
    this.fetchDetail();
  }
};
</script>

<style lang="scss" scoped>
.details {
  color: #686868;
  font-size: 14px;
  margin-bottom: 20px;
  .top {
    border-bottom: 2px solid #afafaf;
    margin-bottom: 20px;
    p {
      text-align: right;
      margin-bottom: 5px;
    }
  }
  .detail {
    text-align: left;
    img {
      width: 100%;
    }
    p {
      text-indent: 28px;
    }
  }
}
</style>



