<template>
  <div class="activity">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>景区概况</BreadcrumbItem>
        <BreadcrumbItem>旅游手记</BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>
          <li v-for="(item, index) in listData" :key="index">
            <Card>
              <router-link :to="{ name:'travelsDetail', params: { travelsId: item.aId }}">
                <div class="list-item">
                  <div class="image" :style="{backgroundImage: 'url('+ item.sThumbnail + ')'}"></div>
                  <div class="info">
                    <h2 class="title">{{item.sTitle}}</h2>
                    <p class="time">{{item.time | dateFormat}}</p>
                    <p class="abstract">{{item.sAbstract}}</p>
                    <p class="link">点击了解详情</p>
                  </div>
                </div>
              </router-link>
            </Card>
          </li>
        </ul>
      </div>
      <Page class="page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/activityBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 10,
      listData: []
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "Travels",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
@import "../styles/variables.scss";
.list {
  margin-bottom: 50px;
  .list-item {
    display: flex;
    justify-content: space-between;
    height: 200px;
    .image {
      width: 35%;
      background-repeat: no-repeat;
      background-size: 100% 100%;
    }
    .info {
      width: 65%;
      padding: 15px 10px;
      text-align: left;
      position: relative;
      color: #000;
      .title {
        color: $blue;
        margin-bottom: 10px;
      }
      .abstract {
        margin-bottom: 10px;
      }
      .time {
        color: $blue;
      }
      .link {
        color: $blue;
        position: absolute;
        bottom: 15px;
        right: 10px;
      }
    }
  }
}
.page {
  margin-bottom: 20px;
}
</style>





