<template>
  <div class="hotel">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>门票特产</BreadcrumbItem>
        <BreadcrumbItem>宾馆住宿</BreadcrumbItem>
      </Breadcrumb>
      <div class="list">
        <ul>
          <li class="list-item" v-for="(item, index) in listData" :key="index">
            <Card>
              <div class="card">
                <img :src="item.pic">
                <a :href="item.url">
                  <h2>点击预定</h2>
                </a>
              </div>
            </Card>
            <div class="info">
              <p>{{item.name}}:{{item.address}}</p>
              <p> 咨询电话：{{item.phone}}</p>
            </div>
          </li>
        </ul>
        <Page class="common-page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import photoWall from "@/assets/photoWall1.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: "",
      pageTotal: 0,
      pageSize: 9,
      listData: []
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/commercial/", {
          params: {
            name: "Hotel",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
            _this.topImage = res.data.topPic;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.list {
  margin-bottom: 50px;
  text-align: left;
}
.list-item {
  display: inline-block;
  width: 31%;
  margin: 0 1% 30px 1%;
  text-align: center;
  .card {
    height: 260px;
    img {
      width: 100%;
      height: 90%;
      display: block;
    }
  }
  .info {
    padding: 10px 0;
  }
}
</style>





