<template>
  <div class="activity">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>景区概况</BreadcrumbItem>
        <BreadcrumbItem>景区概览</BreadcrumbItem>
      </Breadcrumb>
      <Row type="flex" justify="space-between">
        <i-col span="5">
          <Card class="card">
            <div class="nav-image" :style="{backgroundImage: 'url('+ navImage + ')'}"></div>
            <Menu v-if="listData.length>0" width="auto" :active-name="0" @on-select="handleNavChange" id="left_nav">
              <menu-item v-for="(item,index) in listData" :key="index" :name="index" :class="{'nav-item': true}">
                {{item.sTitle}}
              </menu-item>
            </Menu>
          </Card>
        </i-col>
        <i-col span="18">
          <div v-if="articleDetail!=={} " class="common-article article">
            <h1>{{articleDetail.sTitle}}</h1>
            <img :src="articleDetail.sTopPic">
            <div v-html="articleDetail.sText"></div>
          </div>
        </i-col>
      </Row>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/scanBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 10,
      listData: [],
      navCurIndex: 0,
      articleDetail: {}
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "ScenicSpots",
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.listData = res.data.list;
            _this.navCurIndex = 0;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    handleNavChange(name) {
      this.navCurIndex = parseInt(name);
      this.fetchDetail();
    },
    fetchDetail() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/" + this.listData[this.navCurIndex].aId)
        .then(function(res) {
          if (res.data) {
            _this.articleDetail = res.data;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    }
  },
  computed: {
    navImage() {
      if (this.listData.length !== 0)
        return this.listData[this.navCurIndex].sThumbnail;
    }
  },
  watch: {
    listData() {
      this.fetchDetail();
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
@import "../styles/variables.scss";
.nav-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 20px auto;
}
.card {
  margin-top: 55px;
}
.article {
  margin-top: 55px;
}
</style>





