<template>
  <div class="notic-detail">
    <TopImage :image="topImage" />
    <div class="content">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>实用信息</BreadcrumbItem>
        <BreadcrumbItem>关于我们</BreadcrumbItem>
      </Breadcrumb>
      <div class="details">
        <Row>
          <i-col span="16" class="detail">
            <h1>{{data.sTitle}}</h1>
            <div class="info">
              <p>
                <Icon type="android-time"></Icon> 创建：{{data.time}}</p>
              <p>
                <Icon type="edit"></Icon> {{data.sOrganization}}</p>
            </div>
            <div class="common-article" v-html="data.sHtml"></div>
          </i-col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/banner.png";
import ArticleCarouselItem from "@/components/ArticleCarouselItem.vue";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageTotal: 0,
      pageSize: 16,
      data: {},
    };
  },
  methods: {
    fetchDetail() {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "AboutUs",
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.pageTotal = res.data.count;
            _this.data = res.data.list[0];
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    fetchList(page) {
      var _this = this;
      this.$http
        .get(config.ApiRoot + "/article/", {
          params: {
            name: "Notice",
            pageCount: 20
          }
        })
        .then(function(res) {
          if (res.data) {
            _this.listData = res.data.list;
          }
        })
        .catch(function(error) {
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    getItemCount(i){
      if(i*this.perCarouseCount<=this.listData.length) return this.perCarouseCount;
      else return (this.listData.length-((i-1)*this.perCarouseCount));
    },
    getCarouselItemData(i, j) {
      return this.listData[i * this.perCarouseCount + j];
    }
  },
  components: {
    TopImage,
    ArticleCarouselItem
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    $route: "fetchDetail"
  },
  created() {
    this.fetchDetail();
    this.fetchList();
  }
};
</script>

<style lang="scss" scoped>
.details {
  color: #686868;
  font-size: 14px;
  text-align: left;
  margin-bottom: 20px;
  .detail {
    h1 {
      margin: 30px 0;
    }
    .info {
      margin-bottom: 20px;
    }
  }
}
.list {
  padding: 15px;
}
</style>



